* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html, body {
    background: #000000 url("images/bg.jpg") no-repeat center center/cover;
    height: 100vh;
    color: #FFFFFF;
    font-family: Arial, Helvetica, sans-serif;
}

a {
    text-decoration: none;
}

.App {
    max-width: 1100px;
    margin: auto;
    padding: 0 20px;
}

header {
    height: 200px;
}

header img {
    width: 200px;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.search {
    height: 100px;
}

input[type='text'] {
    display: block;
    padding: 10px;
    font-size: 20px;
    border: 0;
    border-radius: 5px;
    width: 100%;
    margin-bottom: 40px;
}

.cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.card {
    cursor: pointer;
    background-color: transparent;
    height: 300px;
    perspective: 1000px;
}

.card h1 {
    font-size: 25px;
    border-bottom: 1px solid white;
    padding-bottom: 10px;
    margin-bottom: 10px;
}

.card img {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.card:hover .card-inner{
    transform: rotateY(180deg);
}

.card-front, .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.card-back {
    background-color: #333333;
    color: white;
    padding: 20px;
    transform: rotateY(180deg);
}

.card li {
    list-style: none;
}

@media all and (max-width: 609px) {
    * {
        font-size: 12px;
    }

    .cards {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media all and (max-width: 720px) and (min-width: 610px) {
    * {
        font-size: 12px;
    }

    .cards {
        grid-template-columns: repeat(3, 1fr);
    }
}